.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #1b1e21;
  height: 62px;
  color: #fff;
  font-size: 25px;
  z-index: 100;
  &__container {
    max-width: 1230px;
    margin: 0 auto;
    height: 100%;
    &__inner {
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}
